/* Чекбоксы */
.program__filter-option label {
  position: relative;
  margin-left: 40px; }

.program__filter-option label::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -40px;
  width: 30px;
  height: 30px;
  border: 2px solid #F2F2F2;
  cursor: pointer; }

.filter-input-checkbox:checked + label::before {
  content: '';
  position: absolute;
  top: -3px;
  left: -40px;
  width: 30px;
  height: 30px;
  background-image: url("../img/icon-tick.svg");
  background-size: 15px 15px;
  background-repeat: no-repeat;
  background-position: center; }

/* Радио батоны */
.filter-input-radio + label {
  position: relative;
  margin-left: 40px; }

.filter-input-radio + label::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -40px;
  width: 30px;
  height: 30px;
  border: 2px solid #CDCDCD;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0.6; }

.filter-input-radio + label:hover::before {
  border-color: #BABABA; }

.filter-input-radio:checked + label::after {
  content: "";
  position: absolute;
  top: 5px;
  left: -32px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #68B738;
  border-color: #BABABA; }
  @media (min-width: 1440px) {
    .filter-input-radio:checked + label::after {
      top: 5px;
      left: -32px; } }

